.all-elements {
  position: relative;
  padding-top: 26px;
  text-align: center;

  &::before {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    margin: 0 auto;
    background-image: url("../img/icon-plus.svg");
  }
}

.all-elements__title {
  margin-top: 23px;
  margin-bottom: 0;
  font-family: $oswald;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
}

.all-elements__desc {
  margin-top: 8px;
  margin-bottom: 20px;
  font-family: $arial;
  font-size: 12px;
  line-height: 16px;
}

.all-elements__show-all {
  display: block;
  width: auto;
  color: $color-grey;
  background-color: $bg-grey;

  &:hover {
    background-color: #ebebeb;
  }

  &:active {
    color: $color-black-active;
  }
}

@media (min-width: $tablet-width) {
  .all-elements {
    padding: 0 38px;
    padding-top: 54px;

    &::before {
      width: 60px;
      height: 60px;
      opacity: 0.2;
    }
  }

  .all-elements__title {
    margin: 0 50px;
    margin-top: 49px;
    margin-bottom: 28px;
    font-size: 20px;
    line-height: 20px;
  }

  .all-elements__desc {
    margin: 0 40px;
    margin-bottom: 86px;
    font-size: 16px;
  }

  .all-elements__show-all {
    font-size: 16px;
  }
}

@media (min-width: $desktop-width) {
  .all-elements {
    padding: 0 38px;
    padding-top: 54px;
  }

  .all-elements__title {
    margin-left: 0;
    margin-right: 0;
  }

  .all-elements__desc {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 86px;
  }
}
